<template>
  <div>
    <el-table
      :data="resumeList"
      v-if="departmentId"
      style="width: 100%">
      <el-table-column
        prop="currentStatus.value"
        label="处理状态">
      </el-table-column>
      <el-table-column
        prop="username"
        label="姓名">
      </el-table-column>
      <el-table-column
        prop="job.name"
        label="岗位">
      </el-table-column>
      <el-table-column
        prop="form.value"
        label="来源">
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID">
      </el-table-column>
      <el-table-column
        prop="mailSize"
        label="大小(kb)">
      </el-table-column>
      <el-table-column
        prop="integral"
        label="积分">
      </el-table-column>
      <el-table-column
        prop="operationUser.name"
        label="操作人员">
      </el-table-column>
      <el-table-column
        prop="operationTime"
        label="处理时间">
        <template slot-scope="scope">
          {{getTimeFormat(scope.row.operationTime)}}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="380">
        <template slot-scope="scope">
          <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)">查看</button>
          <button class="com-btn com-edit" @click="handleEmailDetail(scope.row)">邮件正文</button>
          <button class="com-btn com-history" @click="handleHistory(scope.row)">历史状态</button>
          <!--<button class="com-btn com-op" @click="handleOperate(scope.row)">操作</button>-->
        </template>
      </el-table-column>
    </el-table>

    <div class="com-page-box-dialog">
      <div class="com-pages">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="resumeListTotal" v-if="resumeListTotal > 10">
        </el-pagination>
      </div>
    </div>

    <div v-if="!departmentId" style="padding: 30px 50px;text-align: center;border-bottom: 1px dashed #ccc;color: #888;">
      请在右上角选择部门
    </div>

    <!--查看-->
    <com-dialog
      :show="detailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">处理状态:</span>{{resumeData.processedStatus}}</p>
        <p><span class="label">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名:</span>{{resumeData.name}}</p>
        <p><span class="label">岗&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;位:</span>{{resumeData.pos}}</p>
        <p><span class="label">来&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;源:</span>{{resumeData.from}}</p>
        <p><span
          class="label">I&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;D:</span>{{resumeData.id}}
        </p>
        <p><span class="label">大&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;小:</span>{{resumeData.size}}kb</p>
        <p><span class="label">积&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;分:</span>{{resumeData.integral}}分</p>
        <p><span class="label">操作人员:</span>{{resumeData.processedUser}}</p>
        <p><span class="label">处理时间:</span>{{resumeData.processedTime}}</p>
        <p><span class="label">电话号码:</span>{{resumeData.phone}}</p>
        <!--<p><span class="label">无法联系:</span></p>-->
      </div>

      <div slot="title" class="com-dialog-title t-1">
        查看信息
        <div class="close x-color-1" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--邮件正文-->
    <com-dialog
      :show="emailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;题:</span>{{resumeData2.mailTitle}}</p>
        <!--<p><span class="label">时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;间:</span>{{getTimeFormat(resumeData2.mailSendTime)}}</p>-->
        <p><span class="label">收&nbsp;&nbsp;件&nbsp;&nbsp;人:</span>{{resumeData2.mailId}}</p>

        <p class="com-dashed-line"></p>

        <p><span class="label label-block">
          正&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;文:</span>
          <a target="_blank" :href="'http://192.168.1.158:8080/oa' + resumeData2.mailSavePath">点击查看</a></p>

        <p class="com-dashed-line"></p>

        <p>
          <span class="label label-block">附&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;件:</span>
          <span class="label-block" v-for="f in resumeData.enclosures">{{f.name}}&nbsp;&nbsp;<a target="_blank" :href="serverBaseUrlCpt + f.path">下载</a></span>
        </p>
      </div>

      <div slot="title" class="com-dialog-title t-2">
        邮件正文
        <div class="close x-color-2" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #FEC71E; border-color: #FEC71E;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--邮件历史状态-->
    <com-dialog
      :show="historyStatusDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <el-table
        :data="resumeHistoryList"
        style="width: 100%">
        <el-table-column
          prop="processTime"
          label="处理时间">
          <template slot-scope="scope">
            {{getTimeFormat(scope.row.operationTime)}}
          </template>
        </el-table-column>

        <el-table-column
          prop="statusSmall.value"
          label="状态">
        </el-table-column>

        <el-table-column
          prop="resume.id"
          label="ID">
        </el-table-column>

        <el-table-column
          prop="operationUser.name"
          label="操作人员">
        </el-table-column>

      </el-table>

      <!--<div class="com-page-box-dialog">-->
      <!--<div class="com-pages">-->
      <!--<el-pagination-->
      <!--@size-change="handleSizeChange"-->
      <!--@current-change="handleCurrentChange"-->
      <!--:current-page="currentPage"-->
      <!--:page-sizes="pageSizes"-->
      <!--:page-size="pageSize"-->
      <!--layout="total, sizes, prev, pager, next, jumper"-->
      <!--:total="resumeListTotal">-->
      <!--</el-pagination>-->
      <!--</div>-->
      <!--</div>-->

      <div slot="title" class="com-dialog-title t-3">
        历史状态
        <div class="close x-color-3" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #5AD1D1; border-color: #5AD1D1;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--操作-->
    <com-dialog
      :show="operateDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div>
        <ul class="op-sel">
          <li @click="opSelect(0)" :class="{active: isActive[0]}">待初试(无法确定初试时间) <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(1)" :class="{active: isActive[1]}">预约初试(更改面试时间) <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(2)" :class="{active: isActive[2]}">初试淘汰 <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(3)" :class="{active: isActive[3]}">初试失约 <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(4)" :class="{active: isActive[4]}">无法联系 <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(5)" :class="{active: isActive[5]}">预约复试 <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(6)" :class="{active: isActive[6]}">待复试(无法确定复试时间) <i class="sprites icon-hook"></i></li>
          <li @click="opSelect(7)" :class="{active: isActive[7]}">放弃复试 <i class="sprites icon-hook"></i></li>
        </ul>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        操作邮件（单选）
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--预约初试（更改面试时间）-->
    <com-dialog
      :show="changeInitTestTimeDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label>更改预约时间：</label>
          <el-date-picker
            v-model="initTestForm.time"
            type="datetime"
            placeholder="选择日期时间">
          </el-date-picker>
        </p>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        预约初试（更改面试时间）
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">确定
        </el-button>
      </div>
    </com-dialog>

    <!--无法联系-->
    <com-dialog
      :show="notReachedDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label>电话时间：</label>
          <el-date-picker
            v-model="initTestForm.time"
            type="datetime"
            placeholder="选择日期时间">
          </el-date-picker>
        </p>
      </div>
      <div slot="title" class="com-dialog-title t-4">
        无法联系
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">确定
        </el-button>
      </div>
    </com-dialog>

    <!--预约复试-->
    <com-dialog
      :show="reTestDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <div class="test-form">
        <p>
          <label>初试时间：</label>
          <el-date-picker
            v-model="initTestForm.time"
            type="datetime"
            placeholder="选择日期时间">
          </el-date-picker>
        </p>
        <p>
          <label>发送通知：</label>
          <el-input
            type="textarea"
            :rows="6"
            placeholder="请输入内容"
            v-model="initTestForm.msg">
          </el-input>
        </p>
        <!--<p>-->
        <!--<label>打印简历：</label>-->
        <!--<el-checkbox-group v-model="initTestForm.print">-->
        <!--<el-checkbox label="打印简历" name="type"></el-checkbox>-->
        <!--<el-checkbox label="打印面试情况表" name="type"></el-checkbox>-->
        <!--</el-checkbox-group>-->
        <!--</p>-->
      </div>
      <div slot="title" class="com-dialog-title t-4">
        预约复试
        <div class="close x-color-4" @click="closeDialog">x</div>
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #EB6941; border-color: #EB6941;" type="primary"
                   @click="saveOperate">确定
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import { formatDateTime } from '../../../../utils/utils'
  import comDialog from '../../../../components/toast/dialog.vue'
  import { mapState, mapActions } from 'vuex'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        currentStatusId: 9,
        resumeData: {
          processedStatus: '',
          name: '',
          pos: '',
          from: '',
          id: '',
          size: '',
          integral: '',
          processedUser: '',
          processedTime: ''
        },
        resumeData2: {
          resume: {
            mailSendTime: 0
          }
        },
        detailDialogVisible: false,
        emailDialogVisible: false,
        historyStatusDialogVisible: false,
        operateDialogVisible: false,
        isActive: [false, false, false, false, false, false, false, false],
        opStatusId: 0, // 操作状态id
        changeInitTestTimeDialogVisible: false, // 更改初试
        notReachedDialogVisible: false, // 无法联系
        reTestDialogVisible: false, // 预约复试
        initTestForm: {
          time: '',
          msg: '',
          print: [],
        }
      }
    },
    computed: {
      ...mapState('resume', [
        'resumeList',
        'resumeListTotal',
        'resumeHistoryList',
        'operationRecordAddStatus',
      ]),
      ...mapState([
        'departmentId',
        'searchValue'
      ]),
    },
    watch: {
      departmentId () {
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            resume: {job: {department: {id: this.departmentId}}}
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      searchValue (d) {
        if (d) {
          this.getResumeList()
        }
      },
      operationRecordAddStatus (d) {
        if (d.status) {
          this.getResumeList()
          this.closeDialog()
        }
      }
    },
    methods: {
      ...mapActions('resume', [
        'ac_resumeList',
        'ac_resumeHistoryList',
        'ac_OperationRecordAdd',
      ]),
      handleDetail (row) {
        this.detailDialogVisible = true
        this.resumeData = {
          processedStatus: row.currentStatus.value,
          name: row.username,
          pos: row.job.name,
          from: row.form.value,
          id: row.id,
          size: row.mailSize,
          integral: row.integral,
          processedUser: row.operationUser.name,
          processedTime: this.getTimeFormat(row.operationTime),
          currentStatusId: row.currentStatus.id,
          phone: row.phone,
        }
      },
      handleEmailDetail (row) {
        this.emailDialogVisible = true
        this.resumeData2 = row
      },
      handleHistory (row) {
        this.historyStatusDialogVisible = true
        this.ac_resumeHistoryList({
          resume: {id: row.id}
        })
      },
      handleOperate (row) {
        this.operateDialogVisible = true
        this.resumeData = row
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.getResumeList()
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.getResumeList()
      },
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
      closeDialog () {
        this.detailDialogVisible = false
        this.emailDialogVisible = false
        this.historyStatusDialogVisible = false
        this.operateDialogVisible = false
        this.resumeData = {
          processedStatus: '',
          name: '',
          pos: '',
          from: '',
          id: '',
          size: '',
          integral: '',
          processedUser: '',
          processedTime: ''
        }
        this.changeInitTestTimeDialogVisible = false // 更改初试
        this.notReachedDialogVisible = false // 无法联系
        this.reTestDialogVisible = false // 预约复试
        this.initTestForm = {
          time: '',
          msg: '',
          print: [],
        }
        this.isActive = [false, false, false, false, false, false, false, false]
      },
      opSelect (index) {
        let arr = [false, false, false, false, false, false, false]
        arr[index] = true
        this.isActive = arr
        switch (index) {
          case 0:
            this.opStatusId = 10 // 待初试(无法确定初试时间)
            this.saveOperate()
            break
          case 1:
            this.opStatusId = 32 // 预约初试（更改面试时间)
            this.operateDialogVisible = false
            this.changeInitTestTimeDialogVisible = true // 更改初试
            break
          case 2:
            this.opStatusId = 15 // 初试淘汰
            this.saveOperate()
            break
          case 3:
            this.opStatusId = 16 // 初试失约
            this.saveOperate()
            break
          case 4:
            this.opStatusId = 17 // 无法联系
            this.operateDialogVisible = false
            this.notReachedDialogVisible = true // 无法联系
            break
          case 5:
            this.opStatusId = 14 // 预约复试
            this.operateDialogVisible = false
            this.reTestDialogVisible = true // 预约复试
            break
          case 6:
            this.opStatusId = 19 // 待复试(无法确定复试时间）
            this.saveOperate()
            break
          case 7:
            this.opStatusId = 18 // 放弃复试
            this.saveOperate()
            break
          default:
        }
      },
      saveOperate () {
        let param = {
          resume: {
            id: this.resumeData.id
          },
          statusSmall: {
            id: this.opStatusId
          }
        }
        if (this.initTestForm.time) {
          param.bespeakTime = Date.parse(this.initTestForm.time)
        }
        if (this.initTestForm.msg) {
          param.remarks = this.initTestForm.msg
        }
        this.ac_OperationRecordAdd(param)
      },
      getResumeList () {
        if (this.searchValue) {
          this.ac_resumeList({
            entity: {
              statusBig: {id: this.currentStatusId},
              resume: {job: {department: {id: this.departmentId}}},
              key: this.searchValue
            },
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        } else {
          this.ac_resumeList({
            entity: {
              statusBig: {id: this.currentStatusId},
              resume: {job: {department: {id: this.departmentId}}},
            },
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        }
      }
    },
    components: {
      comDialog,
    },
    beforeCreate () {},
    created () {
      if (this.departmentId) {
        this.ac_resumeList({
          entity: {
            statusBig: {id: this.currentStatusId},
            resume: {job: {department: {id: this.departmentId}}},
          },
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      }
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../../styles/common";
  @import "../../../../styles/sprites/sprites.css";

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #FDC61E;
  }

  .x-color-2 {
    color: #FDC61E;
  }

  .t-3 {
    background-color: #5AD1D1;
  }

  .x-color-3 {
    color: #5AD1D1;
  }

  .t-4 {
    background-color: #EB6941;
  }

  .x-color-4 {
    color: #EB6941;
  }

  .resume-detail {
    p {
      line-height: 40px;
    }
    .label {
      margin-right: 20px;
      &.label-block {
        display: block;
      }
    }
  }

  .op-sel {
    li {
      border-radius: 5px;
      background-color: #EFEFEF;
      margin-bottom: 20px;
      padding: 8px 20px;
      color: #646464;
      font-size: 12px;
      cursor: pointer;
      position: relative;
      i {
        position: absolute;
        right: 20px;
        display: none;
      }
      &.active {
        background-color: #4294E7;
        color: #fff;
        i {
          display: block;
        }
      }
    }
  }
  .test-form {
    p {
      padding: 10px;
    }
  }
</style>



