<template>
  <div>
    <ul class="nav">
      <!--<li class="memuLi" @click="handleNav({name: '通用', id: -1}, 0)">通用关键字</li>-->
      <li class="memuLi" v-for="(nv, i) in nav" @click="handleNav(nv, i)">{{nv.name}}</li>
    </ul>
    <slot></slot>
  </div>
</template>
<script>
  import $ from 'jquery'
  export default {
    name: '',
    props: {
      nav: {
        default: [],
        type: Array,
        require: true
      }
    },
    data () {
      return {}
    },
    computed: {},
    methods: {
      handleNav (nav, i) {
        this.$emit('navBack', {nav: nav, index: i})
        this.navActive(i)
      },
      navActive (i) {
        $('.memuLi').removeClass('active')
        $('.memuLi').eq(i).addClass('active')
      }
    },
    components: {},
    beforeCreate () {},
    created () {},
    beforeMount () {},
    mounted () {
      this.navActive(0)
    },
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  .nav {
    display: flex;
    height: 26px;
    margin-top: 20px;
    margin-left: 20px;
    li {
      margin-right: 10px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      background-color: #fff;
      border: 1px solid #B3B3B3;
      border-bottom: 0px;
      color: #B3B3B3;
      padding: 3px 8px 0 8px;
      font-size: 12px;
      cursor: pointer;
      &.active {
        background-color: #64A5E7;
        color: #fff;
        border-color: #64A5E7;
      }
    }
  }
</style>



