<template>
  <div>
    <content-title
      title="初试列表"
      tips="初试列表，已处理邮件分发至对应栏目。">
      <el-select v-model="value" placeholder="请选择部门" class="sel-dep" @change="selectDepartment">
        <el-option
          v-for="item in departmentList"
          :key="item.id"
          :label="item.name"
          :value="item.id"
          v-if="item.id !== adminDepartmentIdCpt">
        </el-option>
      </el-select>
    </content-title>
    <nav-bar :nav="nav" @navBack="navBack">
      <el-input
        class="com-search"
        placeholder="用户名/ID"
        @change="searchValueChange"
        v-model="searchVal">
        <i slot="prefix" class="el-input__icon el-icon-search"></i>
      </el-input>
    </nav-bar>
    <content-c>
      <component :is="currentView"></component>
    </content-c>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import navBar from '../../componentsBody/navBar.vue'

  import todo from './todo/todo.vue'
  import done from './done/done.vue'
  import { mapState, mapMutations, mapActions } from 'vuex'
  import { adminDepartmentId } from '../../../utils/baseConfig'
  export default {
    name: '',
    props: {},
    data () {
      return {
        nav: [
          {name: '待处理'},
          {name: '已处理'}
        ],
        searchVal: '',
        currentView: todo,
        options: [{
          value: '1',
          label: 'android'
        }, {
          value: '2',
          label: 'IOS'
        }, {
          value: '3',
          label: '前端'
        }, {
          value: '4',
          label: '后端'
        }, {
          value: '5',
          label: 'ui/ue'
        }],
        value: ''
      }
    },
    computed: {
      ...mapState('department', [
        'departmentList',
      ]),
      ...mapState([
        'departmentId'
      ]),
      adminDepartmentIdCpt () {
        return adminDepartmentId
      }
    },
    methods: {
      ...mapActions('department', [
        'ac_departmentList'
      ]),
      ...mapMutations([
        'mut_departmentId',
        'mut_searchValue',
      ]),
      navBack (menu) {
        switch (menu.index) {
          case 0:
            this.currentView = todo
            break
          case 1:
            this.currentView = done
            break
          default:
            this.currentView = todo
        }
      },
      selectDepartment () {
        this.mut_departmentId(this.value)
      },
      searchValueChange () {
        this.mut_searchValue(this.searchVal)
      }
    },
    components: {
      contentTitle,
      contentC,
      navBar,
      todo,
      done,
    },
    beforeCreate () {},
    created () {
      this.ac_departmentList()
    },
    beforeMount () {},
    mounted () {
      if (this.departmentId) {
        this.value = this.departmentId
      }
    },
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";
  .sel-dep {
    width: 180px;
    float: right;
    margin-right: 10px;
    margin-top: -45px;
  }
</style>



